@import url("https://fonts.googleapis.com/css?family=Charmonman");
@keyframes enterFromAbove {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes enterFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes enterFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.col-container {
  display: block;
}

.col-container .row {
  display: flex;
  display: flex;
  display: flex;
  display: flex;
  display: flex;
  display: flex;
  display: flex;
}

.col-container .row [class*="col-"] {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

.col-container .row .col-1-7 {
  width: 14.28571%;
}

.col-container .row .col-2-7 {
  width: 28.57143%;
}

.col-container .row .col-3-7 {
  width: 42.85714%;
}

.col-container .row .col-4-7 {
  width: 57.14286%;
}

.col-container .row .col-5-7 {
  width: 71.42857%;
}

.col-container .row .col-6-7 {
  width: 85.71429%;
}

.col-container .row .col-7-7 {
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.app-body {
  background: url("../images/image.png");
  font-size: 14px;
  height: 100vh;
  font-family: 'Open Sans', sans-serif;
}

.app-header {
  display: flex;
  height: 15%;
  justify-content: space-between;
  align-items: center;
  background: #4a4a49;
  animation: enterFromAbove 1s ease-out;
}

.app-header h1 {
  height: 100%;
  padding: 0 20px;
  display: flex;
  align-items: center;
  transition: 0.2s;
}

.app-header h1:active {
  background: #646462;
}

.app-header a {
  font-size: 30px;
  text-decoration: none;
}

.app-header-panel {
  font-size: 25px;
  height: 100%;
  display: flex;
  align-items: center;
}

.app-header-panel button {
  height: 100%;
  line-height: 100%;
  padding: 0px 20px;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-decoration: none;
  cursor: pointer;
  font-size: 50px;
  transition: 0.4s;
  position: relative;
}

.app-header-panel button:hover {
  background: #7d7d7c;
}

.app-header-panel button:before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  transition: 0.4s;
  margin-right: 20px;
}

.app-header-panel button:active {
  background: #4a4a49;
}

.app-header-panel button.active:before {
  background: #E58A20;
}

.app-header-panel .name {
  font-size: 25px;
  color: #fff;
  padding-right: 20px;
  transition: 0.5s;
  opacity: 0;
}

.app-header-panel i {
  color: #E58A20;
  transition: 0.4s;
}

.main {
  display: flex;
  height: 85%;
}

.main-side-panel {
  background: #5B605F;
  width: 300px;
  animation: enterFromLeft 1s;
}

.main-side-panel-buttons {
  display: flex;
  flex-direction: column;
}

.main-side-panel-buttons button {
  color: #fff;
  text-align: left;
  height: 80px;
  font-size: 20px;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.4s;
}

.main-side-panel-buttons button span {
  display: inline-block;
  padding-left: 20px;
  transition: 0.4s;
}

.main-side-panel-buttons button:before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  transition: 0.4s;
}

.main-side-panel-buttons button:after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border: 1px solid transparent;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translate(0, -50%) rotate(-45deg);
  transition: 1s;
}

.main-side-panel-buttons button.active {
  font-weight: 600;
}

.main-side-panel-buttons button.active:after {
  border-right-color: #FF6600;
  border-bottom-color: #FF6600;
}

.main-side-panel-buttons button.active:before {
  background: #E58A20;
}

.main-side-panel-buttons button:hover {
  background: #8e9392;
}

.main-side-panel-buttons button:active {
  background: #4a4a49;
}

.main-content {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.content-container {
  width: 380px;
  margin: 0 auto;
}

.content {
  transition: 1s;
  height: 90%;
  width: 90%;
  position: absolute;
  top: 5%;
  left: 100%;
  opacity: 0;
  background: #FFFFFA;
  border: 1px dashed #5B605F;
  animation: enterFromRight 1s;
}

.content.active {
  left: 5%;
  opacity: 1;
}

.content h3 {
  font-size: 50px;
  color: #4a4a49;
}

.content.user {
  text-align: center;
}

.content.user h3 {
  font-size: 40px;
  color: #4a4a49;
  margin-top: 40px;
  font-weight: 600;
}

.content.user h4 {
  font-size: 24px;
  color: #4a4a49;
  margin: 20px auto;
  font-weight: 600;
}

.content.user #name {
  height: 30px;
  width: 280px;
  margin-bottom: 20px;
  display: block;
  margin: 20px auto;
  padding: 5px;
}

.content.user label {
  position: absolute;
  top: -9999px;
}

.content.user .send-name {
  background: #FF6600;
  color: #fff;
  font-size: 20px;
  padding: 10px 20px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: 0.4s;
}

.content.user .send-name:hover {
  background: #ffa366;
}

.content.user .send-name:active {
  background: #FF6600;
}

.content.user p {
  color: #4a4a49;
  font-size: 18px;
  font-weight: 600;
}

.content span {
  display: block;
  font-size: 20px;
  color: red;
  font-weight: 600;
  margin-top: 60px;
  transition: 1s;
}

.content span.inactive {
  opacity: 0;
}

.content.dashboard {
  background: none;
  border: none;
}

.dashboard-item {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.4s;
}

.dashboard-item.on-top {
  top: 0;
  left: 0;
  z-index: 1;
  opacity: 1;
}

.dashboard-main-container {
  height: 100%;
  width: 100%;
}

.dashboard-main-top-panel {
  height: 35%;
  width: 100%;
  padding-bottom: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dashboard-main-top-panel button span {
  margin: 0;
  font-size: 20px;
  color: #4a4a49;
}

.dashboard-plan-preview {
  height: 65%;
  width: 100%;
  padding: 0 20px;
  background: #FFFFFA;
  border: 1px dashed #4a4a49;
}

.dashboard-plan-preview .container {
  width: 100%;
}

.dashboard-plan-preview .row {
  width: 100%;
  border-bottom: 1px solid #979795;
  padding: 5px 10px;
  font-size: 20px;
}

.dashboard-plan-preview .plan-preview-title {
  font-size: 20px;
  color: #FF6600;
  text-align: center;
  margin: 10px 0;
  padding-bottom: 10px;
  border-bottom: 1px solid #979795;
}

.dashboard-plan-preview .plan-preview-title span {
  display: inline;
  color: #FF6600;
  font-weight: 700;
}

.dashboard-plan-preview .week-day {
  text-align: center;
  font-size: 18px;
  color: #4a4a49;
}

.dashboard-plan-preview .buttons span {
  display: inline;
}

.dashboard-plan-preview .btn-next, .dashboard-plan-preview .btn-prev {
  color: #FF6600;
  position: absolute;
  bottom: 10px;
  cursor: pointer;
  padding: 0 10px;
  transition: 0.4s;
}

.dashboard-plan-preview .btn-next:not(.disabled):hover, .dashboard-plan-preview .btn-prev:not(.disabled):hover {
  background: #e6e6e6;
}

.dashboard-plan-preview .btn-next:not(.disabled):active, .dashboard-plan-preview .btn-prev:not(.disabled):active {
  background: none;
}

.dashboard-plan-preview .btn-next.disabled, .dashboard-plan-preview .btn-prev.disabled {
  color: #bbb;
}

.dashboard-plan-preview .btn-next.disabled span, .dashboard-plan-preview .btn-prev.disabled span {
  color: #ccc;
}

.dashboard-plan-preview .btn-prev {
  left: 10px;
}

.dashboard-plan-preview .btn-next {
  right: 10px;
}

.dashboard-plan-preview .plan-meal-list {
  transition: 0.15s;
}

#add-recipe, #add-plan, #main {
  width: 20%;
  height: 100%;
  background: white;
  margin-right: 40px;
  border: 1px dashed #4a4a49;
