/* -------------------------------------------- */
/* global */
/* -------------------------------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans";
}

.container {
  width: 1250px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.btn {
  height: 50px;
  width: 150px;
  background: #FF6600;
  border: 0;
  outline: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  color: #fff;
}

.hidden {
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.box-submit {
  display: flex;
  justify-content: flex-end;
}

.box-imput {
  padding: 0 10px;
  height: 50px;
  border: 0;
}

/* -------------------------------------------- */
/* logo */
/* -------------------------------------------- */
.logo {
  font-family: Charmonman;
}

#first {
  font-weight: 100;
  color: white;
}

#second {
  color: #E58A20;
  font-weight: 700;
}

.logo {
  font-size: 45px;
}

/* -------------------------------------------- */
/* nav */
/* -------------------------------------------- */
.page-header {
  height: 100px;
  background-color: #4a4a49;
}

.page-header .container {
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.page-nav-list {
  display: flex;
  list-style: none;
}

.page-nav-list a {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 40px;
}

.page-nav-list a:hover {
  color: #FF6600;
}

/* -------------------------------------------- */
/* slider */
/* -------------------------------------------- */
.bgSlider {
  height: 500px;
  display: flex;
  margin: 0 auto;
  color: #ffffff;
  background: url("../img/cooking-dinner-food-76093.jpg") no-repeat center;
}

.slider li {
  display: none;
}

.slider .visible {
  display: inline-block;
}

.hidden {
  display: none;
}

#nextPicture,
#prevPicture {
  color: white;
  padding: 100px 40px;
  font-size: 100px;
  border: 0;
  background: none;
  cursor: pointer;
  outline: none;
}

#nextPicture:active,
#prevPicture:active {
  color: #FF6600;
}

@keyframes animSlider {
  0% {
    background-color: rgba(44, 44, 43, 0);
  }
  100% {
    background-color: rgba(44, 44, 43, 0.65);
  }
}

@keyframes animText {
  0% {
    color: rgba(255, 255, 255, 0);
  }
  100% {
    color: white;
  }
}

/* -------------------------------------------- */
/* slider text*/
/* -------------------------------------------- */
.slider ul {
  list-style: none;
  text-align: center;
  margin: 0 200px;
}

.slider ul li {
  padding: 30px;
  animation: animSlider 1s linear forwards;
  background: rgba(44, 44, 43, 0.65);
  border-radius: 10px;
}

.slider ul span {
  animation: animText 1s forwards;
}

.slider ul p {
  font-size: 20px;
  animation: animText 1s forwards;
}

.spanSlider {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
}

/* -------------------------------------------- */
/* Call to Action*/
/* -------------------------------------------- */
.bgActive {
  background: #4a4a49;
  height: 250px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bgActive .container {
  justify-content: space-between;
}

.callActive {
  width: 250px;
}

.txtActiveSection h3 {
  font-size: 35px;
  font-weight: normal;
  color: #fff;
  margin-bottom: 20px;
}

.txtActiveSection p {
  font-size: 20px;
  color: #fff;
  margin-right: 50px;
}

/* -------------------------------------------- */
/* about App*/
/* -------------------------------------------- */
.aboutApp {
  background: #fff;
  color: #4a4a49;
  font-size: 20px;
  height: 700px;
  display: flex;
  align-items: center;
  padding-top: 100px;
}

.txtCol {
  margin: 0 50px;
  text-align: center;
}

.txtCol h3 {
  font-size: 35px;
  margin-bottom: 20px;
}

.mark {
  position: relative;
}

.clock {
  position: relative;
}

.clipboard {
  position: relative;
}

.mark::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 150px;
  content: "\f00c";
  position: absolute;
  top: -180px;
  right: 80px;
}

.clock::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 200;
  font-size: 150px;
  content: "\f017";
  position: absolute;
  top: -180px;
  right: 80px;
}

.clipboard::before {
  font-family: "Font Awesome 5 Free";
  font-size: 150px;
  content: "\f03a";
  position: absolute;
  top: -180px;
  right: 80px;
}

/* -------------------------------------------- */
/* newsletter*/
